//将价格分转为元
export function money(val) {
  val = parseFloat((val / 100 + '').replace(/[^\d\.-]/g, '')).toFixed(2) + ''
  var l = val
      .split('.')[0]
      .split('')
      .reverse(),
    r = val.split('.')[1]
  var t = ''
  for (var i = 0; i < l.length; i++) {
    t += l[i] + ((i + 1) % 3 == 0 && i + 1 != l.length ? ',' : '')
  }
  return (
    t
      .split('')
      .reverse()
      .join('') +
    '.' +
    r
  )
}
//将字符串隔开
export function strchange(val) {
  var t = ''
  for (var i = 0; i < val.length; i++) {
    t += val[i] + ((i + 1) % 4 == 0 && i + 1 != val.length ? ' ' : '')
  }
  return t
}
//将分钟变成小时
export function toHourMinute(val) {
  return (val / 60).toFixed(1)
}
// DateFormat( new Date(this.arooms.activityTime[1]).getTime(),"yyyy-MM-dd hh:mm:ss");
export function DateFormat(date, fmt) {
  var date = new Date(parseInt(date))
  var o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'q+': Math.floor((date.getMonth() + 3) / 3),
    S: date.getMilliseconds()
  }
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substr(4 - RegExp.$1.length)
    )
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length)
      )
  }
  return fmt
}
//非空验证
export function emptytest(value) {
  var isempty = /^\s*$/g
  if (value === '') {
    return false
  }
  if (isempty.test(value)) {
    return false
  } else {
    return true
  }
}
//电话验证
export function phonetest(value) {
  var isPhone = /^((\+?86)|(\(\+86\)))?(13[0123456789][0-9]{8}|15[0123456789][0-9]{8}|17[0123456789][0-9]{8}|18[0123456789][0-9]{8}|147[0-9]{8}|1349[0-9]{7})$/
  var isfixed = /^([0-9]{3,4})?[0-9]{7,8}$/
  if (value === '') {
    return false
  }
  if (!isPhone.test(value) || !isfixed.test(value)) {
    return false
  } else {
    return true
  }
}
