'use strict';
import axios from 'axios';
import Qs from 'qs';

import { Toast, Indicator } from 'mint-ui';
axios.defaults.baseURL = '/';
// axios.defaults.baseURL = 'http://hotel1.deeptel.com.cn';
// axios.defaults.baseURL = 'https://hotel.deeptel.com.cn';
// axios.defaults.baseURL = 'http://192.168.3.37';
//响应时间
axios.defaults.timeout = 5000;
//`withCredentails`选项表明了是否是跨域请求
// axios.defaults.withCredentials = false;
axios.defaults.withCredentials = true;
//设置默认请求头
axios.defaults.headers = {
  'X-Requested-With': 'XMLHttpRequest',
  'Content-Type': 'application/json',
  'Access-Control-Allow-Headers':
    'Origin, X-Requested-With, Content-Type, Accept',
  'Access-Control-Allow-Credentials': 'true'
};

//判断是否微信登陆
var browser = '';
function isWeiXin() {
  var ua = window.navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) == 'micromessenger') {
    return true;
  } else {
    return false;
  }
}
//添加请求拦截器
axios.interceptors.request.use(
  config => {
    Indicator.open({ spinnerType: 'fading-circle' });
    // 判断是否微信   1  微信  99 不是微信
    if (isWeiXin()) {
      browser = 1;
    } else {
      browser = 99;
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// 响应时拦截
axios.interceptors.response.use(
  function(response) {
    Indicator.close();
    // 返回响应时做一些处理
    if (response.data.code != 0 && response.data.code != 1001) {
      checkStatus(response);
    } else if (response.data.code == 1001) {
      var paeams = encodeURIComponent(
        JSON.stringify({
          browser: browser,
          uclogin: null,
          returnUrl:
            window.location.host +
            '/mobile/78CDF1/home/' +
            response.data.hotelId,
          busId: response.data.busId
        })
      );
      console.log(response.data.redirectUrl + paeams);
      location.href = response.data.redirectUrl + paeams;
    }
    return response.data;
    // return response.data;
  },
  function(error) {
    Indicator.close();
    // 当响应异常时做一些处理
    return Promise.reject(error);
  }
);
//成功的处理错误的回调函数
function checkStatus(response) {
  // 异常状态下，可以弹出一个错误提示，告诉用户
  Toast({
    message: response.data.msg,
    duration: 2000
  });
}
//失败的回调函数
function checkCode(res) {
  // 如果code异常(这里已经包括网络错误，服务器错误，可以弹出一个错误提示，告诉用户
  // Message.error('网络异常');
  // Message.error(res);
  Toast({
    message: '网络异常',
    duration: 2000
  });
  return {
    code: 404
  };
}
export default {
  post(url, params) {
    return axios
      .post(url, params)
      .then(res => {
        return res;
      })
      .catch(err => {
        return checkCode(err);
      });
  },
  get(url, params) {
    return axios
      .get(url, {
        params: params
      })
      .then(res => {
        return res;
      })
      .catch(err => {
        return checkCode(err);
      });
  },
  delete(url, params) {
    return axios
      .delete(url, {
        data: params
      })
      .then(res => {
        return res;
      })
      .catch(err => {
        return checkCode(err);
      });
  }
};



